<template>
  <div class="test">
    <vx-header :showback="1" :title="$t('common.test')"></vx-header>
    <div class="vx-container">
      <div class="test_cont">
        <div class="test_list">
          <div class="test_list_item" :class="{selected:o.testPattern == selected}" v-for="(o,i) in screemList" :key="i" @click="setTest(o)">
            <div class="screem" :class="o.class">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import htp from "../api/http";
  import urls from '../api/apiurl';
  export default {
    data() {
      return {
        selected: '',//选中的测试画面
        screemList: [
          { testPattern: 0 }, { testPattern: 1 }, { testPattern: 2 }, { testPattern: 3 }, { testPattern: 4 }, { testPattern: 5 }, { testPattern: 6 }, { testPattern: 7 }, { testPattern: 16 }, { testPattern: 17 }, { testPattern: 18 },
          { testPattern: 19 }, { testPattern: 20 }, { testPattern: 21 }, { testPattern: 32 }, { testPattern: 33 }, { testPattern: 34 },
          { testPattern: 35 }, { testPattern: 36 }, { testPattern: 37 }, { testPattern: 38 }, { testPattern: 39 }
        ],
        type: '',
        style: {},
        orientation: true,
        rows: 0
      };
    },
    created() {
      this.getTsList();
      this.selected = 1;
    },
    mounted() {
      htp.post(urls.setTestPicture, { testControl: 1, testPattern: this.selected });
    },
    methods: {
      //获取测试屏幕列表
      getTsList() {
        this.TsHandler();
      },
      TsHandler() {
        this.screemList.forEach(ts => {
          ts.class = 'test' + ts.testPattern;
        });
      },
      setTest(selected) {
        this.selected = selected.testPattern;
        this.use(1);
      },
      use(control) {
        htp.post(urls.setTestPicture, { testControl: control, testPattern: this.selected });
      }
    },
    beforeRouteLeave(to, from, next) {
      htp.post(urls.setTestPicture, { testControl: 0, testPattern: 0 });
      next();
    }
  };
</script>

<style lang="less" scoped>
  .test {
    text-align: left;
    .mint-cell {
      background-color: #212124;
      color: #c2c2c2;
      height: 90px;

      .mint-switch-core::before {
        background-color: #ffffff;
      }
      .mint-switch-input:checked + .mint-switch-core {
        background-color: #48cc4b;
      }
    }
    &_list {
      background-color: #383940;
      flex-wrap: wrap;
      display: flex;
      padding-top: 33px;
      padding-left: 36px;
      padding-right: 35px;
      &_item {
        height: 100px;
        margin-bottom: 68px;
        border-radius: 8px;
        overflow: hidden;
        .screem {
          width: 100%;
          height: 100%;
          background-size: 100% 100%;
        }
      }
    }
    .save {
      color: #c16117;
    }
    .test0 {
      background: ~"url('../assets/test/test0.png') no-repeat" #383940;
    }
    .test1 {
      background: ~"url('../assets/test/test1.png') no-repeat" #383940;
    }
    .test2 {
      background: ~"url('../assets/test/test2.png') no-repeat" #383940;
    }
    .test3 {
      background: ~"url('../assets/test/test3.png') no-repeat" #383940;
    }
    .test4 {
      background: ~"url('../assets/test/test4.png') no-repeat" #383940;
    }
    .test5 {
      background: ~"url('../assets/test/test5.png') no-repeat" #383940;
    }
    .test6 {
      background: ~"url('../assets/test/test6.png') no-repeat" #383940;
    }
    .test7 {
      background: ~"url('../assets/test/test7.png') no-repeat" #383940;
    }
    .test16 {
      background: ~"url('../assets/test/test10.png') no-repeat" #383940;
    }
    .test17 {
      background: ~"url('../assets/test/test11.png') no-repeat" #383940;
    }
    .test18 {
      background: ~"url('../assets/test/test12.png') no-repeat" #383940;
    }
    .test19 {
      background: ~"url('../assets/test/test13.png') no-repeat" #383940;
    }
    .test20 {
      background: ~"url('../assets/test/test14.png') no-repeat" #383940;
    }
    .test21 {
      background: ~"url('../assets/test/test15.png') no-repeat" #383940;
    }
    .test32 {
      background: ~"url('../assets/test/test20.png') no-repeat" #383940;
    }
    .test33 {
      background: ~"url('../assets/test/test21.png') no-repeat" #383940;
    }
    .test34 {
      background: ~"url('../assets/test/test22.png') no-repeat" #383940;
    }
    .test35 {
      background: ~"url('../assets/test/test23.png') no-repeat" #383940;
    }
    .test36 {
      background: ~"url('../assets/test/test24.png') no-repeat" #383940;
    }
    .test37 {
      background: ~"url('../assets/test/test25.png') no-repeat" #383940;
    }
    .test38 {
      background: ~"url('../assets/test/test26.png') no-repeat" #383940;
    }
    .test39 {
      background: ~"url('../assets/test/test27.png') no-repeat" #383940;
    }
  }
  .selected {
    border: 3px solid #fd7b00;
  }
  @media screen and (orientation: portrait) {
    .test {
      text-align: left;
      &_list {
        &_item {
          width: 20%;
          height: 93px;
          margin-bottom: 68px;
          margin-left: 6.66%;
          &:nth-child(4n + 1) {
            margin-left: 0;
          }
          .screem {
            width: 100%;
            height: 100%;
            background-size: 100% 100%;
          }
        }
      }
    }
  }
  @media screen and (orientation: landscape) {
    .test {
      text-align: left;
      &_list {
        &_item {
          width: 15%;
          height: 93px;
          margin-bottom: 68px;
          margin-left: 2%;
          &:nth-child(6n + 1) {
            margin-left: 0;
          }
          .screem {
            width: 100%;
            height: 100%;
            background-size: 100% 100%;
          }
        }
      }
    }
  }
</style>



